@import url("https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap");

/*   color variables */
$clr-primary: #d50000;
$clr-primary-hover: #29e6a7;
$clr-primary-dark: #039d69;
$clr-gray100: #f9fbff;
$clr-gray150: #f4f6fb;
$clr-gray200: #eef1f6;
$clr-gray300: #e1e5ee;
$clr-gray400: #767b91;
$clr-gray500: #4f546c;
$clr-gray600: #2a324b;
$clr-gray700: #161d34;

/*   border radius */
$radius: 0.2rem;

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
}

.wrapper_rewards {
  display: grid;
  grid-template-columns: repeat(auto-fit, 120rem);
  grid-template-rows: repeat(auto-fit, 12rem);
  gap: 1rem;
}

.card_rewards {
  overflow: hidden;
  box-shadow: 0 2px 10px $clr-gray300;
  border-radius: $radius;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  transition: transform 200ms ease-in;
  margin-top: 2rem;

  &__image {
    height: 10rem;
    width: 30%;
    object-fit: contain;
  }

  &__title {
    padding: 1rem;
    font-weight: bold;
    font-size: larger;
  }

  &__description {
    padding: 0 1rem;
  }

  &__btn {
    padding: 1rem;
    font-family: inherit;
    font-weight: bold;
    font-size: 1rem;
    margin: 1rem;
    background: transparent;
    color: $clr-primary;
    border-radius: $radius;
    transition: 200ms ease-in, color 100ms ease-in;
    position: absolute;
    right: 0px;
  }
}